.row {
    padding: 15px 0;
    background-color: #ffffff;
}

.row--alt {
    background-color: $light-colour;
    color: $dark-colour;
}

.row--brand {
    background-color: $brand-colour;
    color: #ffffff;
}

.row--secondary {
    background-color: $secondary-colour;
    color: $dark-colour;
}

.row--flush {
    padding: 0;
}

.row__heading {
    width: 100%;
    margin: 12px 0;
    font-family: "Oswald", sans-serif;
    font-size: 22px;
    line-height: 24px;
    font-weight: 700;
}
.row__heading:first-child {
    margin-top: 0;
}

.row__sub-heading {
    width: 100%;
    margin: 8px 0;
    font-family: "Oswald", sans-serif;
    font-size: 16px;
    line-height: 18px;
    font-weight: 700;
}

.row__column-image {
    width: 75%;
    max-width: 165px;
}

/* Only for Desktops */
@media only screen and (min-width: $tablet-width) {
    .row {
        padding: 30px 0;
    }
    .row--flush {
        padding: 0;
    }

    .row__heading {
        margin: 24px 0;
        font-size: 26px;
        line-height: 30px;
    }

    .row__sub-heading {
        margin: 10px 0;
        font-size: 18px;
        line-height: 20px;
    }

    .row--halves {
        > .container {
            display: flex;
            align-items: center;
        }

        .row__column {
            width: 50%;

            &:first-child {
                padding-right: 30px;
            }

            &:last-child {
                padding-left: 30px;
            }
        }

        .row__column--flush.row__column--flush {
            padding-left: 0;
        }
    }
}

@media only screen and (min-width: $desktop-width) {
    .row--thirds {
        > .container {
            display: flex;
            align-items: center;

            .row__column {
                width: 33%;

                &:first-child {
                    padding-right: 30px;
                }

                &:not(:first-child):not(:last-child) {
                    padding: 0 15px;
                }

                &:last-child {
                    padding-left: 30px;
                }
            }

            .row__heading {
                margin: 0;
            }
        }
    }
}
